@value colorWhite,colorBlue from "../../common/color.css";
@value borderRadius,border from "../../common/size.css";

.container {
  display: flex;
  flex-direction: column;
  padding: 0.3em;
}

.container.row {
  flex-direction: row;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  width: 2.2rem;
  height: 2.2rem;
  background-color: transparent;
  border: border;
  border-radius: 50%;
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: colorWhite;
  margin-right: 0.3em;
  cursor: pointer;
  transform: rotate(90deg);
  transition: transform ease 300ms;
}

.btn.active,
.btn:hover {
  background-color: colorWhite;
  color: colorBlue;
  transform: none;
}

.title {
  margin: 0;
  font-size: 1.5rem;
}

@media screen and (min-width: 700px) and (max-width: 800px) {
  .btn {
    font-size: 2rem;
    width: 2rem;
    height: 2rem;
  }

  .title {
    font-size: 1.3rem;
  }
}

@media screen and (min-width: 550px) and (max-width: 700px) {
  .btn {
    font-size: 1.5rem;
    width: 1.8rem;
    height: 1.8rem;
  }
  .title {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 550px) {
  .btn {
    font-size: 1.5rem;
    width: 1.6rem;
    height: 1.6rem;
  }

  .title {
    font-size: 1.2rem;
  }
}
